.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
width:50%;
height: 50%;
  }
}

.App-header {
  background-color: #ffffff;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #dc9932;
  font-size: 10px;
}

.Form {
  min-height: 1vh;
}

.Label-style{
  color:white;
}

.App-link {
  color: #61dafb;
}

.Switch{
  color: black;
}
.Switch.Mui-checked{ /* Increase the specificity */
  color: blue;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
